.floatingHi {
  -webkit-animation: fly 45s linear infinite;
          animation: fly 45s linear infinite;
  top: 0;
  left: 0;
  position: fixed;
  z-index: 100;
  pointer-events: none;
}

/* https://wickydesign.com/make-an-image-fly-across-the-screen/ */
@-webkit-keyframes fly {
	0% {
		-webkit-transform: translate(-2em,20em);
		        transform: translate(-2em,20em);
	}

	3% {
		-webkit-transform: translate(-2em,20em) ;
		        transform: translate(-2em,20em) ;
	}

	25% {
		-webkit-transform: translate(45em,-10em);
		        transform: translate(45em,-10em);
	}

	50% {
		-webkit-transform: translate(50em,50em) ;
		        transform: translate(50em,50em) ;
	}

	62.5% {
		-webkit-transform: translate(25em,10em) ;
		        transform: translate(25em,10em) ;
	}

	75% {
		-webkit-transform: translate(100em,50em) ;
		        transform: translate(100em,50em) ;
	}

	100% {
		-webkit-transform: translate(0em,20em) ;
		        transform: translate(0em,20em) ;
	}
}
@keyframes fly {
	0% {
		-webkit-transform: translate(-2em,20em);
		        transform: translate(-2em,20em);
	}

	3% {
		-webkit-transform: translate(-2em,20em) ;
		        transform: translate(-2em,20em) ;
	}

	25% {
		-webkit-transform: translate(45em,-10em);
		        transform: translate(45em,-10em);
	}

	50% {
		-webkit-transform: translate(50em,50em) ;
		        transform: translate(50em,50em) ;
	}

	62.5% {
		-webkit-transform: translate(25em,10em) ;
		        transform: translate(25em,10em) ;
	}

	75% {
		-webkit-transform: translate(100em,50em) ;
		        transform: translate(100em,50em) ;
	}

	100% {
		-webkit-transform: translate(0em,20em) ;
		        transform: translate(0em,20em) ;
	}
}

